import PropTypes from 'prop-types';
import s from './FrienList.module.css';
import defaultImg from '../Profile_comp/default-user2.png';

import { FaChrome } from 'react-icons/fa';

export default function FriendListItem({ id, avatar, name, isOnline  = true }) {
  // const noAvatar = " " || undefined || null || false;
  if (avatar !== true) {
    avatar = defaultImg;
  }

  return (
    <li className={s.friend} key={id}>
       <span className={s.status} style={{color: isOnline ? "green" : "red"}}>< FaChrome /></span>
      <img className={s.avatar} src={avatar} alt={name} width="48" />
      <p className={s.name}>{name}</p>
    </li>
  )
}

FriendListItem.propTypes = {
  id: PropTypes.number.isRequired,
  avatar: PropTypes.string,
  name: PropTypes.string.isRequired,
  isOnline: PropTypes.bool.isRequired,
}
